<template>
  <div class="list-wrapper">
    <div class="search-box">
      <div class="price">
        <div class="label">ALD {{$t('index.price')}}</div>
        <div class="num">${{aldPrice}}</div>
      </div>
      <div class="box">
        <img src="~images/search-icon.png" alt="">
        <input type="text" v-model="searchToken" :placeholder="$t('index.enter')">
      </div>
    </div>
    <div class="list">
      <div class="list-item" v-for="i in filteredList" :key="i.idNum">
        <div class="item-head">
          <span class="icon">
            <img :src="$tokenLogo(i.mainToken)" alt="">
            <img :src="$tokenLogo(i.deputyToken)" alt="">
          </span>
<!--          <span class="level">1X</span>-->
        </div>
        <div class="item-des">
          <div>
            <span class="label">{{$t('index.pledge')}}</span>
            <span class="value">{{$tokenSymbol(i.mainTokenSympol)}} - {{$tokenSymbol(i.deputyTokenSympol)}}</span>
          </div>
          <div>
            <span class="label">{{$t('index.earn')}}</span>
            <span class="value">ALD</span>
          </div>
          <div>
            <van-popover v-model="i.showTip" placement="top-start" theme="dark" trigger="click">
              <div class="status-tip">{{$t('index.tip')}}</div>
              <template #reference>
                <span class="label">
                  APR({{$t('index.highest')}})
                  <img src="~images/question1.png" alt="">
                </span>
              </template>
            </van-popover>
            <span class="value">{{i.apr}}%</span>
          </div>
        </div>
        <div class="item-btn">
          <span @click="$router.push({name: 'Pledge', query: {id: i.id.toString()}})" class="left">{{$t('index.pledge')}}</span>
          <span @click="toRedeem" class="right">{{$t('index.redeem')}}</span>
        </div>
      </div>
    </div>
    <Redeem v-if="showRedeem" @hide="showRedeem = false" />
  </div>
</template>

<script>
import Redeem from '@/components/Redeem'

export default {
  components: {
    Redeem
  },
  props: ['list', 'aldPrice'],
  data () {
    return {
      searchToken: '',
      showRedeem: false
    }
  },
  computed: {
    filteredList () {
      if (this.searchToken) {
        return this.list.filter(i => i.mainTokenSympol.toLowerCase().indexOf(this.searchToken.toLowerCase()) > -1)
      } else {
        return this.list
      }
    }
  },
  methods: {
    toRedeem () {
      this.showRedeem = true
    }
  }
}
</script>

<style lang="less" scoped>
.search-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 14px;
  .price {
    .label {
      color: #666666;
      font-size: 12px;
      font-weight: 500;
    }
    .num {
      color: @theme-color;
      font-size: 13px;
      font-weight: bold;
    }
  }
  .box {
    display: flex;
    align-items: center;
    width: 60%;
    background: #fff;
    height: 32px;
    padding: 0 10px;
    border-radius: 16px;
    img {
      width: 14px;
      margin-right: 8px;
    }
    input {
      flex: 1;
      font-size: 13px;
      border: none;
      &:focus {
        outline: none;
      }
      &::placeholder {
        color: #999;
      }
    }
  }
}
.list {
  padding: 0 14px;
  .list-item {
    background: #fff;
    border-radius: 5px;
    margin-bottom: 15px;
    overflow: hidden;
    &:nth-last-of-type(1) {
      margin-bottom: 0;
    }
    .item-head {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 45px;
      padding: 0 12px;
      border-bottom: 1px solid #DEDEDE;
      .icon {
        display: flex;
        align-items: center;
      }
      img {
        display: block;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        margin-right: 10px;
      }
      .level {
        width: 42px;
        text-align: center;
        line-height: 20px;
        border-radius: 4px;
        background: @theme-gradient;
        color: @theme-text;
        font-size: 12px;
        font-weight: bold;
      }
    }
    .item-des {
      padding: 8px 12px;
      line-height: 26px;
      div {
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
      .label {
        color: #666666;
        font-size: 13px;
        font-weight: 500;
        display: flex;
        align-items: center;
        img {
          margin-left: 4px;
        }
      }
      .value {
        font-size: 13px;
        color: #111111;
        font-weight: bolder;
      }
    }
    .item-btn {
      height: 37px;
      display: flex;
      span {
        width: calc(~'50% + 20px');
        line-height: 37px;
        text-align: center;
        color: #111111;
        font-size: 13px;
        font-weight: 500;
      }
      .left {
        background: url(~images/left-bg.png) no-repeat;
        background-size: 100% 100%;
        background-position: left center;
        margin-right: -20px;
      }
      .right {
        background: url(~images/right-bg.png) no-repeat;
        background-size: 100% 100%;
      }
    }
  }
}
</style>
