import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import i18n from './i18n'
import { Toast, Popup, Popover, Slider, Picker, List } from 'vant'
import 'vant/lib/toast/style'
import 'vant/lib/popup/style'
import 'vant/lib/popover/style'
import 'vant/lib/slider/style'
import 'vant/lib/picker/style'
import 'vant/lib/list/style'
import '@/utils/ethers'

Vue.use(List)
Vue.use(Picker)
Vue.use(Slider)
Vue.use(Popover)
Vue.use(Popup)
Vue.use(Toast)
Vue.config.productionTip = false
Vue.prototype.$tokenLogo = (address) => {
  const path = `https://xuruiju.gitee.io/assets/blockchains/smartchain/assets/${address}/logo.png`
  return path
}
Vue.prototype.$tokenSymbol = symbol => {
  return (String(symbol) === 'WETH' || String(symbol) === 'WBNB') ? 'BNB' : String(symbol)
}

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')
