<template>
  <div v-show="show" class="progress-wrapper">
    <div class="bar" :style="{width: progress + '%'}"></div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      show: false,
      progress: 0
    }
  },
  computed: {
    status () {
      return this.$store.state.routeProgress
    }
  },
  watch: {
    status (val) {
      if (!val) {
        this.progress = 0
        this.show = true
        this.loading()
      } else {
        this.progress = 100
      }
    },
    progress (val) {
      if (val === 100) {
        setTimeout(() => {
          this.progress = 0
          this.show = false
        }, 500)
      }
    }
  },
  mounted() {
    this.show = true
    this.loading()
  },
  methods: {
    loading () {
      let add = () => {
        if (this.progress < 100) {
          this.progress++
          setTimeout(add, 50)
        }
      }
      setTimeout(add, 50)
    }
  }
}
</script>

<style lang="less" scoped>
.progress-wrapper {
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  top: 0;
  .bar {
    height: 2px;
    background: @theme-color;
    transition: all .5s;
  }
}
</style>
