import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/Index'
import store from '../store'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Index',
    component: Index,
    meta: {
      keepAlive: true
    }
  },
  {
    path: '/calc/i',
    name: 'CalcI',
    component: () => import(/* webpackChunkName: "CalcI" */ '../views/Calc/i.vue')
  },
  {
    path: '/calc/team',
    name: 'CalcTeam',
    component: () => import(/* webpackChunkName: "CalcTeam" */ '../views/Calc/team.vue')
  },
  {
    path: '/share',
    name: 'Share',
    component: () => import(/* webpackChunkName: "Share" */ '../views/Share')
  },
  {
    path: '/pledge',
    name: 'Pledge',
    component: () => import(/* webpackChunkName: "Pledge" */ '../views/Pledge')
  },
  {
    path: '/upgrade',
    name: 'Upgrade',
    component: () => import(/* webpackChunkName: "Upgrade" */ '../views/Upgrade')
  },
  {
    path: '/notice',
    name: 'Notice',
    component: () => import(/* webpackChunkName: "Notice" */ '../views/Notice')
  }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  store.commit('updateRouteProgress', 0)
  next()
})

router.afterEach(() => {
  setTimeout(() => {
    store.commit('updateRouteProgress', 1)
  }, 0)
})

export default router
