import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    showActiveAccount: false,
    routeProgress: 0,
    lang: '',
    address: '',
    inviteAddress: '',
    signer: null,
    isActive: false
  },
  mutations: {
    updateActiveAccount (state, value) {
      state.showActiveAccount = value
    },
    updateRouteProgress (state, value) {
      state.routeProgress = value
    },
    updateLang (state, value) {
      state.lang = value
    },
    updateAddress (state, value) {
      state.address = value
    },
    updateInviteAddress (state, value) {
      state.inviteAddress = value
    },
    updateSigner (state, value) {
      state.signer = value
    },
    updateIsActive (state, value) {
      state.isActive = value
    }
  },
  actions: {
  },
  modules: {
  }
})
