let commonMixin = {
  computed: {
    address () {
      return this.$store.state.address
    },
    isActive () {
      return this.$store.state.isActive
    }
  },
  watch: {
    address (a) {
      this.onAddress ? this.onAddress(a) : ''
    }
  },
  methods: {
    active () {
      this.$store.commit('updateActiveAccount', true)
    }
  }
}

export { commonMixin }
