<template>
  <div v-show="show" class="toast-mask" @click="hide">
    <div class="toast-box" @click.stop="noop" :class="{show: showBox}">
      <div class="box">
        <div class="title">{{$t('toast.aa')}}</div>
        <textarea class="input-box" v-model="inviteAddress" :placeholder="$t('toast.ip')"></textarea>
        <div class="tip">{{$t('toast.tip')}}</div>
        <div class="btn">
          <span class="cancel" @click="hide">{{$t('toast.cancel')}}</span>
          <span class="confirm" @click="bind">{{$t('toast.confirm')}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { commonMixin } from '@/utils/mixins'

export default {
  mixins: [ commonMixin ],
  data () {
    return {
      showBox: false,
      inviteAddress: this.$store.state.inviteAddress || ''
    }
  },
  computed: {
    show () {
      return this.$store.state.showActiveAccount
    }
  },
  watch: {
    show (val) {
      if (val) {
        setTimeout(() => {
          this.showBox = true
        }, 100)
      } else {
        this.showBox = val
      }
    }
  },
  methods: {
    bind () {
      if (!this.inviteAddress) {
        this.$toast(this.$t('toast.ip'))
        return
      }
      this.$toast.loading({
        message: this.$t('tip.loading'),
        duration: 0,
        forbidClick: true
      })
      this.$ald.bindIntro(this.inviteAddress).then(() => {
        this.$store.commit('updateIsActive', true)
        this.$toast.clear()
        this.$toast.success(this.$t('toast.as'))
        this.hide()
      })
      .catch(() => {
        this.$toast.clear()
        this.$toast.fail(this.$t('toast.af'))
      })
      console.log(this.$ald, 'ald')
    },
    hide () {
      this.showBox = false
      this.inviteAddress = ''
      setTimeout(() => {
        this.$store.commit('updateActiveAccount', false)
      }, 550)
    },
    noop () {}
  }
}
</script>

<style lang="less" scoped>
.toast-mask {
  position: fixed;
  z-index: 200;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  .toast-box {
    position: absolute;
    background: @theme-color;
    width: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #fff;
    box-sizing: border-box;
    padding: 20px 0;
    border-radius: 5px;
    transition: width .6s;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    &.show {
      width: calc(100vw - 40px);
    }
    .box {
      flex-shrink: 0;
      width: calc(100vw - 40px);
    }
    .title {
      font-size: 16px;
    }
    textarea {
      width: calc(100% - 50px);
      padding: 10px;
      color: #111;
      border-radius: 5px;
      border: 1px solid #dfe2e5;
      text-align: center;
      margin-top: 10px;
      font-size: 14px;
    }
    .tip {
      color: #653405;
      font-size: 12px;
      margin-top: 4px;
    }
    .btn {
      margin-top: 40px;
      span {
        display: inline-block;
        line-height: 30px;
        padding: 0 20px;
        border-radius: 5px;
        border: 1px solid #fff;
        margin: 0 10px;
        font-size: 13px;
      }
    }
  }
}
</style>
