<template>
  <div class="wrapper">
    <Progress />
    <div class="header">
      <span class="left">
        <img @click="showNav = true" src="~images/head-nav.png" alt="">
      </span>
      <span class="center">{{$t('index.ald')}}</span>
      <span class="right">
      <van-popover
        v-model="showLang"
        trigger="click"
        :actions="langList"
        placement="bottom-end"
        @select="onSelect"
      >
        <template #reference>
          <img :src="langImg" alt="">
        </template>
      </van-popover>
    </span>
    </div>
    <van-popup v-model="showNav" position="left">
      <div class="side-nav">
        <Info @hide="showNav = false" />
        <Nav @hide="showNav = false" />
      </div>
    </van-popup>
  </div>
</template>

<script>
import Progress from './progress'
import Info from './info'
import Nav from './nav'
import store from "@/store"

export default {
  components: {
    Progress,
    Info,
    Nav
  },
  data () {
    return {
      showLang: false,
      showNav: false,
      langList: [
        {
          text: '',
          lan: 'zh-cn',
          img: require('images/zh-cn.png')
        },
        {
          text: '',
          lan: 'zh-tw',
          img: require('images/zh-tw.png')
        },
        {
          text: 'English',
          lan: 'en',
          img: require('images/en.png')
        }
      ]
    }
  },
  computed: {
    langImg () {
      let lang = this.$store.state.lang || 'zh-cn'
      let idx = ['zh-cn', 'zh-tw', 'en'].indexOf(lang)
      return this.langList[idx].img
    }
  },
  methods: {
    onSelect(action) {
      this.$i18n.locale = action.lan
      store.commit('updateLang', action.lan)
      localStorage.setItem('lang', action.lan)
    }
  }
}
</script>

<style lang="less" scoped>
.wrapper {
  position: fixed;
  z-index: 100;
  width: 100%;
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 44px;
    background: #fff;
    box-sizing: border-box;
    padding: 0 10px;
  }
  .left, .right {
    width: 20%;
    display: flex;
    align-items: center;
  }
  .right {
    justify-content: flex-end;
  }
  img {
    display: block;
    width: 28px;
  }
  .center {
    width: 60%;
    text-align: center;
    color: #000000;
    font-size: 18px;
    font-weight: bold;
  }
  .side-nav {
    height: 100vh;
    width: 250px;
    background: #fff;
  }
}
</style>
