<template>
  <div class="toast-mask" @click="$emit('hide')">
    <div class="toast-box" @click.stop="noop">
      <div class="toast-title">
        <span class="gradient-title">{{$t('redeem.rt')}}</span>
      </div>
      <div class="toast-info">
        <div class="info">
          <span>{{$t('redeem.po')}}</span>
          <span @click="showPicker = true">{{order.text}}</span>
        </div>
        <div class="info">
          <span>{{$t('redeem.p')}}{{pInfo ? $tokenSymbol(pInfo.mainTokenSympol) : '--'}}{{$t('redeem.n')}}</span>
          <span>{{order.mainTokenAmount}} {{pInfo ? $tokenSymbol(pInfo.mainTokenSympol) : ''}}</span>
        </div>
        <div class="info">
          <span>{{$t('redeem.p')}}{{pInfo ? $tokenSymbol(pInfo.deputyTokenSympol) : '--'}}{{$t('redeem.n')}}</span>
          <span>{{order.deputyTokenAmount}} {{pInfo ? $tokenSymbol(pInfo.deputyTokenSympol) : ''}}</span>
        </div>
        <div class="info">
          <span>{{$t('redeem.pp')}}</span>
          <span>{{order.balance}}</span>
        </div>
        <van-slider v-model="percent" :min="1" class="slider" active-color="#FFC000" />
        <div class="slider-tip">
          <div class="min-max">
            <span>1</span>
            <span>100</span>
          </div>
          <div class="current">
            <span>{{percent}}%</span>
          </div>
        </div>
      </div>
      <div class="toast-tip">
        <div class="tip-title">{{$t('redeem.rtt')}}</div>
        <div class="tip">{{$t('redeem.rtip')}}</div>
      </div>
      <div @click="redeem" class="redeem-btn theme-btn">
        <span>{{$t('redeem.rt')}}</span>
      </div>
    </div>
    <van-popup v-model="showPicker" round position="bottom" @click.stop="noop">
      <van-picker
          show-toolbar
          :columns="orders"
          @cancel="showPicker = false"
          @confirm="onConfirm"
      />
    </van-popup>
  </div>
</template>

<script>
import { commonMixin } from '@/utils/mixins'

export default {
  mixins: [ commonMixin ],
  data () {
    return {
      percent: 1,
      showPicker: false,
      rates: null,
      order: {
        text: this.$t('redeem.stip'),
        id: '',
        stakeId: '',
        rate: 0,
        balance: '--',
        deputyTokenAmount: '--',
        mainTokenAmount: '--'
      },
      pInfo: null,
      orders: []
    }
  },
  created () {
    this.getAllRateType()
  },
  watch: {
    order (val) {
      if (val.id) {
        this.getProduct(val.stakeId)
        this.percent = val.rate
      }
    }
  },
  methods: {
    onAddress () {
      this.getAllRateType()
    },
    getOrders () {
      this.$ald.getUserRecordStruct(String(this.address)).then(records => {
        let l = []
        records.forEach(r => {
          if(Number(r.balance.toString()) > 0) {
            let t = `${this.$t('redeem.o')}${r.id.toString()}:${this.$formatEther(r.mainTokenAmount)}-${this.$formatEther(r.deputyTokenAmount)}`
            l.push({
              text: t,
              id: r.id.toString(),
              stakeId: r.stakeType.toString(),
              rate: this.rates[r.rateType.toString()],
              balance: this.$formatEther(r.balance),
              deputyTokenAmount: this.$formatEther(r.deputyTokenAmount),
              mainTokenAmount: this.$formatEther(r.mainTokenAmount)
            })
          }
        })
        this.orders = l
        console.log(records)
      })
    },
    async getProduct (id) {
      this.pInfo = await this.$ald.getProduct(id)
    },
    getAllRateType () {
      this.$ald.getAllRateTypeStruct().then(rates => {
        let res = {}
        rates.forEach(r => {
          res[r.id.toString()] = Number(r.mainRate.toString()) / 100
        })
        this.rates = res
        this.getOrders()
      })
    },
    onConfirm(value) {
      this.order = value
      this.showPicker = false
    },
    async redeem () {
      if (!this.order || !this.pInfo) {
        this.$toast(this.$t('redeem.stip'))
        return
      }
      this.$toast.loading({
        message: this.$t('tip.loading'),
        duration: 0,
        forbidClick: true
      })
      console.log(this.order.id, (this.percent * 100).toString(), 'args...')
      this.$ald.withdrawRecord(this.order.id, (this.percent * 100).toString()).then(() => {
        this.$toast.clear()
        this.$toast.success(this.$t('redeem.rs'))
        this.resetOrder()
      }).catch(() => {
        this.$toast.clear()
        this.$toast.fail(this.$t('redeem.rf'))
      })
    },
    resetOrder () {
      this.order = {
        text: this.$t('redeem.stip'),
        id: '',
        stakeId: '',
        rate: 0,
        balance: '--',
        deputyTokenAmount: '--',
        mainTokenAmount: '--'
      }
      this.percent = 1
      this.pInfo = null
      this.orders = []
      this.getOrders()
    },
    noop () {}
  }
}
</script>

<style lang="less" scoped>
.toast-mask {
  position: fixed;
  z-index: 200;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  .toast-box {
    position: absolute;
    background: #fff;
    width: 90%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 5px;
    padding-bottom: 30px;
    .toast-title {
      padding: 8px 12px;
      border-bottom: 1px solid #ddd;
    }
    .toast-info {
      padding: 15px;
      .info {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 12px;
      }
      .slider {
        margin-top: 30px;
      }
      .slider-tip {
        padding-top: 10px;
        .min-max {
          display: flex;
          align-items: center;
          justify-content: space-between;
        }
        .current {
          text-align: center;
          margin-top: 2px;
        }
      }
    }
    .toast-tip {
      padding: 0 10px;
      .tip {
        font-size: 14px;
      }
    }
    .redeem-btn {
      margin-top: 20px;
    }
  }
}
</style>
