/* eslint-disable */
import Vue from 'vue'
import { ethers, BigNumber } from 'ethers'
import { Contract, Provider } from 'ethers-multicall'
import aldAbi from '@/abi/ald.json'
import { aldAddr } from '@/utils/addr'
import store from '@/store'

if (window.ethereum) {
  const mainChainId = '0x38' // bian test
  const provider = new ethers.providers.Web3Provider((ethereum))
  const aldContract = new ethers.Contract(aldAddr, aldAbi, provider)

  console.log(aldContract)

  provider.on('network', network => {
    checkChain(network.chainId)
  })

  Vue.prototype.$provider = provider
  Vue.prototype.$callProvider = new Provider(provider)
  Vue.prototype.$formatEther = ethers.utils.formatEther
  Vue.prototype.$parseEther = ethers.utils.parseEther
  Vue.prototype.$ald = aldContract
  Vue.prototype.$callProvider.init().then(() => {
    Vue.prototype.$callAld = new Contract(aldAddr, aldAbi)
  })

  ethereum.on('accountsChanged', accounts => {
    setAccount(accounts)
  })

  ethereum.on('chainChanged', chainId => {
    checkChain(chainId)
  })

  function checkChain (chainId) {
    if(chainId !== Number(mainChainId)) {
      ethereum.request({
        method: 'wallet_switchEthereumChain',
        params: [{ chainId: mainChainId }]
      })
    }
  }

  function setAccount (accounts) {
    let account = accounts[0] || ''
    let signer = account ? provider.getSigner() : null
    if (signer) {
      Vue.prototype.$ald = aldContract.connect(signer)
    }
    if (account) {
      Vue.prototype.$ald.isActive(account).then(res => {
        store.commit('updateIsActive', res)
      })
    }
    store.commit('updateAddress', account)
    store.commit('updateSigner', signer)
    if (!accounts.length) {
      requestAccount()
    }
  }

  function requestAccount () {
    ethereum.request({ method: 'eth_requestAccounts' }).then(accounts => {
      setAccount(accounts)
    }).catch(err => {
      console.log(err, 'connect error')
    })
  }

  requestAccount()
}
