<template>
  <div class="info-wrapper">
    <div class="address-box">
      <div class="tl">
        <span class="title">Address</span>
        <span class="level">Lv {{level}}</span>
      </div>
      <div class="address clip-button" :data-clipboard-text="address">
        <span>{{address}}</span>
        <img src="~images/copy.png" alt="">
      </div>
      <div class="sb">
        <van-popover
            v-model="showStatusTip"
            placement="top-start"
            theme="dark"
            trigger="click">
          <div class="status-tip">
            <div>{{$t('side.tip1')}}</div>
            <div>{{$t('side.tip2')}}</div>
            <div>{{$t('side.tip3')}}</div>
          </div>
          <template #reference>
            <span class="status">
              <span>{{$t('side.state')}}{{address ? $t('side.connected') : $t('side.unconnected')}}</span>
              <img src="~images/question.png" alt="">
            </span>
          </template>
        </van-popover>
        <span v-if="address && !isActive" @click="active" class="btn">{{$t('side.active')}}</span>
      </div>
    </div>
    <div class="info-box">
      <div @click="to('CalcI')">
        <span class="label">
          <img src="~images/up-icon.png" alt="">
          <span>{{$t('side.pp')}}</span>
        </span>
        <span class="value">{{userTotalPower - teamPower}}</span>
      </div>
      <div @click="to('CalcTeam')">
        <span class="label">
          <img src="~images/tp-icon.png" alt="">
          <span>{{$t('side.tp')}}</span>
        </span>
        <span class="value">{{teamPower}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import Clipboard from "clipboard"
import { commonMixin } from '@/utils/mixins'

export default {
  mixins: [ commonMixin ],
  data () {
    return {
      showStatusTip: false,
      teamPower: '0.000',
      userTotalPower: '0.000',
      level: '0.0000'
    }
  },
  created() {
    if (this.address) {
      this.initData()
    }
  },
  mounted() {
    let clipboard = new Clipboard('.clip-button')
    clipboard.on('success', e => {
      e.clearSelection()
      this.$toast(this.$t('tip.cs'))
    })
    clipboard.on('error', () => {
      this.$toast.fail(this.$t('tip.cf'))
    })
  },
  methods: {
    initData () {
      this.getUserTotalPower()
      this.getTeamPower()
      this.getLevel()
    },
    onAddress () {
      this.initData()
    },
   getLevel () {
      this.$ald.userLevel(this.address).then(level => {
        this.level = level.toString()
      })
    },
    getUserTotalPower () {
      this.$ald.balanceOf(this.address).then(res => {
        this.userTotalPower = Number(res.toString()/Math.pow(10, 18)).toFixed(4)
      })
    },
    getTeamPower () {
      this.$ald.teamPower(this.address).then(res => {
        this.teamPower = Number(res.toString()/Math.pow(10, 18)).toFixed(4)
      })
    },
    to (name) {
      this.$emit('hide')
      this.$router.push({name})
    }
  }
}
</script>

<style lang="less" scoped>
.info-wrapper {
  padding: 10px;
}
.address-box {
  background: @theme-color;
  border-radius: 5px;
  color: #fff;
  padding: 12px;
  .tl {
    .title {
      font-size: 24px;
      display: inline-block;
      margin-right: 20px;
    }
    .level {
      font-size: 12px;
    }
  }
  .address {
    font-size: 12px;
    word-break: break-all;
    margin-top: 16px;
    line-height: 18px;
    img {
      margin-left: 4px;
      margin-bottom: -4px;
    }
  }
  .sb {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-top: 4px;
    .status {
      font-size: 12px;
      display: flex;
      align-items: center;
      span {
        white-space: nowrap;
      }
      img {
        margin-left: 2px;
        margin-bottom: -2px;
      }
    }
    .btn {
      line-height: 24px;
      padding: 0 6px;
      border-radius: 5px;
      border: 1px solid #fff;
      font-size: 12px;
      white-space: nowrap;
      margin: 0 0 0 2px;
    }
  }
}
.info-box {
  padding: 0 10px 0 20px;
  > div {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .label {
    font-size: 14px;
    color: #373737;
    font-weight: bold;
    display: flex;
    align-items: center;
    img {
      width: 17px;
      margin-right: 12px;
    }
  }
  .value {
    font-size: 14px;
    color: #373737;
  }
}
</style>
