<template>
  <div class="top-board">
    <div>
      <div class="name">Be VIP</div>
      <div class="des">{{$t('index.level')}}v{{info.level}}</div>
      <div class="btn">
        <span @click="toUpgrade">{{$t('index.get')}}</span>
      </div>
    </div>
    <div>
      <div class="name">ALD EARNED</div>
      <div class="des">
        <div>{{info.reward}} ALD</div>
        <div> ${{info.usdtReward}}</div>
      </div>
      <div class="btn" @click="drawReward">
        <span>{{$t('index.receive')}}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['info'],
  methods: {
    toUpgrade () {
      if (!this.$store.state.isActive) {
        this.info.active()
      } else {
        this.$router.push({name: 'Upgrade'})
      }
    },
    drawReward () {
      this.$emit('drawReward')
    }
  }
}
</script>

<style lang="less" scoped>
.top-board {
  display: flex;
  align-items: center;
  padding: 10px 14px 0;
  > div {
    width: 50%;
    height: 104px;
    background: url(~images/board-bg.png) no-repeat;
    background-size: 100% 100%;
    box-sizing: border-box;
    padding: 8px 12px 0;
    &:nth-of-type(1) {
      margin-right: 10px;
    }
  }
  .name {
    color: @theme-text;
    font-size: 15px;
    font-weight: bold;
  }
  .des {
    color: @theme-text;
    font-size: 12px;
    font-weight: bold;
    height: 40px;
    margin-top: 4px;
  }
  .btn {
    span {
      display: inline-block;
      border: 1px solid @theme-text;
      border-radius: 4px;
      line-height: 22px;
      width: 53px;
      text-align: center;
      color: @theme-text;
      font-size: 12px;
      font-weight: bold;
    }
  }
}
</style>
