<template>
  <div class="wrapper">
    <Board @drawReward="drawReward" :info="boardInfo" />
    <List :aldPrice="aldPrice" :list="list" />
    <BottomInfo :info="bottomInfo"/>
  </div>
</template>

<script>
import Board from './board'
import List from './list'
import BottomInfo from './bottomInfo'
import { USDT, Ald } from '@/utils/addr'
import tokenAbi from '@/abi/erc20.json'
import { commonMixin } from '@/utils/mixins'
import { ethers } from "ethers"
// import { Contract } from 'ethers-multicall'

export default {
  components: {
    Board,
    List,
    BottomInfo
  },
  mixins: [ commonMixin ],
  data () {
    return {
      boardInfo: {
        level: '--',
        reward: '0.00000',
        active: this.active,
        usdtReward: '0.00000'
      },
      bottomInfo: {
        burnAmount: '0.000',
        totalPower: '0.000'
      },
      aldPrice: '0.0000',
      list: []
    }
  },
  created () {
    let ia = this.$route.query.inviteAddress || ''
    this.$store.commit('updateInviteAddress', ia)
    if (this.address) {
      this.initData()
    }
  },
  methods: {
    initData () {
      this.getLevel(this.address)
      this.getList()
      this.getTotalPower()
      this.getTotalBurnAmount()
      this.getReward()
    },
    onAddress () {
      this.initData()
    },
    getLevel (a) {
      this.$ald.userLevel(a).then(level => {
        this.boardInfo.level = level.toString()
      })
    },
    async getReward () {
      let price = await this.$ald.getNeedAmount(Ald, USDT, '1000000000000000000', '10000')
      this.aldPrice = Number(price.toString()/Math.pow(10, 18)).toFixed(5)
       this.$ald.earned(this.address).then(amount => {
        this.boardInfo.reward = Number(amount.toString()/Math.pow(10, 18)).toFixed(5)
        this.boardInfo.usdtReward = Number(this.aldPrice * this.boardInfo.reward).toFixed(5)
      })
    },
    drawReward () {
       this.$ald.getReward({from: this.address}).then(() => {
        this.getReward()
      })
    },
    getList () {
      this.$toast.loading({
        message: this.$t('tip.loading'),
        duration: 0,
        forbidClick: true
      })
      let that = this
      this.$ald.getAllProductStruct().then(async (list) => {
        console.log(list, 'list....')
        let l = []
        for (let i = 0; i < list.length; i++) {
          let token = new ethers.Contract(list[i].mainToken, tokenAbi, this.$provider)
          let decimals = await token.decimals()
          let param = (100 * Math.pow(10, Number(decimals.toString()))).toString()
          let apr = await this.$ald.getApr(list[i].mainToken, param, '1000000000000000000')
          l.push({
            ...list[i],
            apr: apr.toString() / 100,
            showTip: false
          })
        }
        that.list = l
        this.$toast.clear()
      })
      .catch((err) => {
        console.log(err)
        this.$toast.clear()
      })
    },
    getTotalPower () {
      this.$ald.totalSupply().then(amount => {
        this.bottomInfo.totalPower = Number(amount / Math.pow(10, 18)).toFixed(4)
      })
    },
    getTotalBurnAmount () {
      this.$ald.totalBurnAmount().then(amount => {
        this.bottomInfo.burnAmount = Number(amount / Math.pow(10, 18)).toFixed(4)
      })
    }
  },
  watch: {
    address (val) {
      if (val) {
        this.initData()
      }
    }
  }
}
</script>

<style lang="less" scoped>
.wrapper {
  padding-bottom: 48px;
}
</style>
